.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

body {
  font-family: "Times New Roman", Times, serif;
  margin: 20px;
  line-height: 1.6;
}

h1 {
  text-align: center;
  background-color: beige;
  color: black;
  padding: 8m;
  border-radius: 20px;
}

header {
  position: sticky;
}

h2 {
  margin: 20px 0;
  background-color: antiquewhite;
  border-radius: 10px;
  text-align: center;
}

video {
  border-radius: 10px;
}

table {
  width: 95%;
  border-collapse: collapse;
  margin: 5px;
}

.right-column {
  flex: 1;
  padding: 20px;
  background-color: #ddd;
  border-left: 12px #ccc;
  border-radius: 10px;
}

.main-content {
  flex: auto;
  padding: 20px;
  background-color: #b2b2b2;
  text-align: justify;
  font-family: "Times New Roman", Times, serif;
  font-weight: 500;
  border-radius: 15px;
}

.page-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
}

.search-container {
  display: flex;
  width: 300px;
  margin: 2px;
  justify-content: flex-start;
}

.search-input {
  width: 100%;
  padding: 15px 30px;
  border: 2px #ccc;
  border-radius: 30px;
  font-size: 12px;
  font: bold;
  outline: none;
  transition: border-color 0.3s ease-in-out;
}

.search-input:hover {
  border-color: #888;
}

button {
  flex: auto;
  background-color: beige;
  color: #888;
  border: none;
  padding: 5px;
  border-radius: 10px;
  height: 30px;
  width: 100px;
  margin-left: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  font-style: bold;
  font-family: "Times New Roman", Times, serif;
  position: relative;
}

button:hover {
  color: tomato;
}

button:active {
  color: blue;
}

.about_content {
  background-color: #b2b2b2;
  text-align: justify;
  font-family: "Times New Roman", Times, serif;
  font-weight: 500;
  border-radius: 10px;
  padding: 10px;
}

.video_ {
  display: flex;
  justify-content: center;
  border-radius: 20px;
}

.images {
  margin: auto;
  padding: auto;
  margin-right: 10px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.images img {
  border-radius: 20px;
  height: auto;
  box-shadow: black;
}